import React,{useState} from "react";
import emailjs from "emailjs-com"

const Contact = () => {

  const [emailData, setEmailData] = useState({
    name: '',
    email: '',
    message: ''
  });
  console.log(emailData);

  const handleChange = (e) => {
    setEmailData({
      ...emailData,
      [e.target.name]: e.target.value
    });
  };
  console.log(emailData,)

const sendMsg = (e) => {
    e.preventDefault();
    emailjs.send('service_g5l4uwg', 'template_6z5etmj', emailData, 'SZcvR3kCH1QuJbR27')
      .then((response) => {
        console.log('Email sent successfully!', response.status, response.text);
        // Reset the form
        setEmailData({
          name: '',
          email: '',
          subject: '',
          message: ''
        });
      })
      .catch((error) => {
        console.error('Error sending email:', error);
      });
  };








  const contact_info = [
    { logo: "mail", text: "azhar1756350@gmail.com" },
    { logo: "logo-whatsapp", text: "03201756350" },
    {
      logo: "location",
      text: "demo location",
    },
  ];
  return (
    <section id="contact" className="py-10 px-3 text-white">
      <div className="text-center mt-8">
        <h3 className="text-4xl font-semibold">
          Contact <span className="text-cyan-600">Me</span>
        </h3>
        <p className="text-gray-400 mt-3 text-lg">Get in touch</p>

        <div
          className="mt-16 flex md:flex-row flex-col
         gap-6 max-w-5xl bg-gray-800 md:p-6 p-2 rounded-lg mx-auto"
        >
          <form className="flex flex-col flex-1 gap-5">
            <input type="text" placeholder="Your Name"  name="name"  onChange={handleChange}   value={emailData.name}/>
            <input type="Email" placeholder="Your Email Address" name="email" onChange={handleChange}  value={emailData.email}/>
            <textarea placeholder="Your Message" name="message"  onChange={handleChange} rows={10} value={emailData.message}></textarea>
            <button  onClick={sendMsg} className="btn-primary w-fit">Send Message</button>
          </form>
          <div className="flex flex-col  gap-7 ">
            {contact_info.map((contact, i) => (
              <div
                key={i}
                className="flex flex-row  
                  text-left gap-4 flex-wrap items-center"
              >
                <div className="min-w-[3.5rem]  text-3xl min-h-[3.5rem] flex items-center justify-center text-white bg-cyan-600 rounded-full">
                  <ion-icon name={contact.logo}></ion-icon>
                </div>
                <p className="md:text-base text-sm  break-words">
                  {contact.text}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
